body {
  min-height: 100vh;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

.container {
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
}

.left-container {
  height: 100vh;
  grid-column: 1 / 2;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.left-items {
  flex-direction: column;
  grid-column: 2 / 3;
  align-items: center;
  display: flex;
}

.left-content-style {
  font-size: 1.5rem;
}

.left-content-style i {
  margin-top: 15px;
  margin-right: 15px;
}

.left-content-style p i {
  margin-top: 15px;
  margin-right: 15px;
  font-weight: 200;
}

.mid-container {
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
  grid-column: 2 / 3;
}

.right-container {
  grid-column: 3 / 4;
}

img {
  max-width: 50px;
}

#userAndImage {
  min-height: 175px;
  border: 1px solid #ccc;
  border-left: none;
  border-right: none;
}

#profileImage {
  height: 40px;
  width: 40px;
  object-fit: cover;
  margin-right: 10px;
}

#postContent::placeholder {
  color: #999;
  font-size: 1.5rem;
}

#postContent {
  height: 30px;
  border: none;
  margin-left: 50px;
  font-size: 1.2rem;
}

#postContent:focus {
  outline: none;
}

#postBtn {
  color: #fff;
  height: 35px;
  color: #fff;
  background-color: #1da1f2;
  border: none;
  border-radius: 20px;
  margin: 5px 0 5px auto;
  padding: 5px 15px;
  font-size: 14px;
}

#postBtn:hover {
  cursor: pointer;
  background-color: #2390d3;
}

#imageAndUsername {
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

#newPostForm {
  flex-direction: column;
  display: flex;
}

#buttonRow {
  border-top: 1px solid #ccc;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  margin-left: 50px;
  margin-right: 50px;
  display: flex;
}

#buttonRow a {
  color: #1da1f2;
  border-radius: 4px;
  padding: 12px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

#buttonRow a:hover {
  background-color: #e6f3ff;
}

.logout-container {
  width: 100%;
  align-items: center;
  padding: 0 1rem;
  position: fixed;
  bottom: 0;
}

.stay-fixed {
  position: fixed;
}

#loggOutBtn {
  color: #fff;
  height: 30px;
  background-color: #1da1f2;
  border: none;
  border-radius: 20px;
  font-size: 14px;
}

#loggOutBtn:hover {
  cursor: pointer;
  background-color: #2390d3;
}

#deleteAccountBtn {
  color: #fff;
  height: 30px;
  background-color: #1da1f2;
  border: none;
  border-radius: 20px;
  margin: 5px 0;
  font-size: 14px;
}

#deleteAccountBtn:hover {
  cursor: pointer;
  background-color: #2390d3;
}

#username {
  font-size: 1.5rem;
  font-weight: 200;
}

.gritter-style {
  text-align: center;
  letter-spacing: 2px;
  font-size: 2rem;
}

.post-item {
  border-bottom: 1px solid #ccc;
  padding-left: 10px;
}

.reply-btn {
  margin-left: 10px;
}

#top10LikedPosts {
  width: 300px;
  background-color: #f1f9fa;
  border-radius: 30px;
  margin-left: 70px;
}

.postLiked10 {
  border-bottom: 1px solid #ccc;
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

#gillade-h3-style, #profil-h3-style {
  margin-top: 40px;
  margin-left: 90px;
  font-size: 1.5rem;
}

.user-container {
  width: 300px;
  min-height: 200px;
  background-color: #6abac5;
  border-radius: 30px;
  margin-left: 70px;
}

.user-names {
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.postName {
  font-size: 1.2rem;
}

.post-actions {
  margin-bottom: 10px;
}

.postContent {
  font-size: 1.1rem;
}

.postTime {
  font-size: .6rem;
}

.fa-heart:hover {
  color: red;
  cursor: pointer;
}

.fa-comment:hover {
  color: #0ff;
  cursor: pointer;
}

.left-content-style a {
  color: inherit;
  text-decoration: none;
}

.user-names p:hover {
  cursor: pointer;
  font-size: 1.1rem;
}

